<template>
    <div class="home container">
        <div>

            <md-field>
                <label for="movie">City / Year</label>
                <md-select v-model="selectedgameid" name="city year" id="movie">
                    <md-option :key="foo.game_id" v-for="foo in Games" :value="foo.city">{{foo.city}}--{{foo.year}}</md-option>

                </md-select>
            </md-field>

            <h1> <strong>Selected city :</strong>
                {{ selectedgameid }}</h1>
            <carousel :navigationEnabled="true" :paginationEnabled="true" :per-page="4" :mouse-drag="false">
                <slide :key="item.index" v-for="(item,index) in Olympicfilter">
                    <!-- <template v-if="item.game_id===foo.game_id"> -->
                    <div class="md-layout md-gutter">
                        <div class="md-layout-item">
                            <md-card>
                                <md-card-media-cover md-solid>
                                    <md-card-media md-ratio="1:1">
                                        <img @click="changed(item.athlete_id)" :src="getImageurl(item.photo.data)" alt="Skyscraper">
                                    </md-card-media>

                                    <md-card-area>
                                        <md-card-header>
                                            <span class="md-title">{{item.name}} {{item.surname}}</span>
                                           <!--  <span class="md-subhead">{{item.game_id}}</span> -->
                                        </md-card-header>

                                        <md-card-actions>

                                            <!-- <md-icon>favorite</md-icon> -->
                                            <h3><img width="50%" src="@/assets/gold.png">{{item.gold}}</h3>
                                            <h3><img width="50%" src="@/assets/silver.png">{{item.silver}}</h3>
                                            <h3><img width="50%" src="@/assets/bronze.png">{{item.bronze}}</h3>

<!-- 
                                            <md-button class="md-icon-button">
                                                <md-icon>share</md-icon>
                                            </md-button> -->
                                        </md-card-actions>
                                    </md-card-area>
                                </md-card-media-cover>
                            </md-card>
                        </div>
                  
                    </div>

                </slide>

            </carousel>

        </div>
        <HelloWorld msg="Welcome to Your Vue.js App" />
    </div>
</template>

<script>
// @ is an alias to /src
import HelloWorld from '@/components/HelloWorld.vue'
import {
    Carousel,
    Slide
} from 'vue-carousel';
import VuePaginator from 'vuejs-paginator'
import {
    store
} from '../store/index'
import router from '../router/index'
export default {
    name: 'Home',
    components: {
        HelloWorld,
        VPaginator: VuePaginator,
        Carousel,
        Slide
    },
    data: () => ({
        Olympic:null,
        Games: null,
        selectedgameid: "Pyeongchang",
        src: "data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7"
    }),

    methods: {
        updateResource(data) {
            this.Olympic = data
        },

        printOlympic() {
            async function getOlympic() {

                const response = await fetch("https://immense-ocean-06133.herokuapp.com/" + "athlete");
                return await response.json();
            }
            getOlympic().then(x => {
             
                x.sort((a, b) => (a.year > b.year) ? 1 : -1)
                _.orderBy(x, ['gold', 'silver'], ['asc', 'desc'])
                this.Olympic = x

            })
        },

        printYear() {

            async function getGames() {

                const response = await fetch("https://immense-ocean-06133.herokuapp.com/" + "game");
                return await response.json();
            };
            getGames().then(x => {

                x.sort((a, b) => (a.year < b.year) ? 1 : -1)
               
                this.Games = x
            })
        },


        getImageurl(response) {
            const intArray = new Uint8Array(response);
            const reducedArray = intArray.reduce((data, byte) => data + String.fromCharCode(byte), '');

            const base64String = `data:image/png;base64, ${btoa(reducedArray)}`;


            return base64String;

        },
        changed(id) {
            this.$store.commit('change', id)
            router.push('result')
            console.log(id)
        }


    },
    mounted() {
        this.printOlympic()
        this.printYear()
    },
    computed: {


        Olympicfilter: function() {
            let selected = this.selectedgameid
            return this.Olympic.filter(u => {

                return u.city.match(this.selectedgameid)
            })

            // console.log(this.Olympic)

        },
    }
}
</script>